import { Box, Stack, Text } from "@chakra-ui/layout";
import { useToast } from "@chakra-ui/toast";
import axios from "axios";
import { useEffect, useState } from "react";
import { getSender } from "../config/ChatLogics";
import { ChatState } from "../context/ChatProvider";
import { useLocation } from "react-router-dom";

const MyChats = ({ fetchAgain }) => {
  const user = JSON.parse(localStorage.getItem("userInfo"));
  const { selectedChat, setSelectedChat, chats, setChats } = ChatState();

  const location = useLocation();
  console.log(location.state.currentUser);
  const [loggedUser, setLoggedUser] = useState(location.state.currentUser);

  useEffect(() => {
    setLoggedUser(location.state.currentUser);
    fetchChats();
    // eslint-disable-next-line
  }, [fetchAgain]);


  const toast = useToast();

  const fetchChats = async () => {
    // console.log(user._id);
    try {
      const config = {
        headers: {
          Authorization: `Bearer ${user.token}`,
        },
      };

      const { data } = await axios.get("/api/chat", config);
      setChats(data);
    } catch (error) {
      toast({
        title: "Error Occured!",
        description: "Failed to Load the chats",
        status: "error",
        duration: 5000,
        isClosable: true,
        position: "bottom-left",
      });
    }
  };

  useEffect(() => {
    setLoggedUser(JSON.parse(localStorage.getItem("userInfo")));
    fetchChats();
    // eslint-disable-next-line
  }, [fetchAgain]);

  return (
    <Box
      display={{ base: selectedChat? "none" : "flex", md: "flex" }}
      flexDir="column"
      alignItems="center"
      p={3}
      bg="white"
      // width={{ base: "100%", md: "31%" }}
      width="100%"
      border="0px 1px 0xp 0px solid #EDEDED"
    >
      <Box
        pb={3}
        px={3}
        fontSize={{ base: "28px", md: "30px" }}
        d="flex"
        w="100%"
        color="#666666"
        justifyContent="space-between"
        alignItems="center"
        fontWeight="500"
      >
        List of chats
      </Box>
      <Box
        d="flex"
        flexDir="column"
        p={3}
        // bg="#F8F8F8"
        w="100%"
        height="100%"
        borderRadius="lg"
        // overflowY="hidden"
        overflowY="scroll"
      >
        {chats ? (
          <Stack>
            {chats.map((chat) => (
              <Box
                onClick={() => setSelectedChat(chat)}
                cursor="pointer"
                bg={selectedChat === chat ? "#008ECC" : "#f3f9fb"}
                color={selectedChat === chat ? "white" : "#666666"}
                px={3}
                py={2}
                key={chat._id}
              >
                <Text>
                  {!chat.isGroupChat
                    ? <b>{getSender(loggedUser, chat.users)}</b>
                    : <b>{chat.chatName}</b>}
                </Text>
                {chat.latestMessage && (
                  <Text fontSize="xs">
                    {chat.latestMessage.sender.name}:&nbsp;
                    {chat.latestMessage.content.length > 50
                      ? chat.latestMessage.content.substring(0, 51) + "..."
                      : chat.latestMessage.content}
                  </Text>
                )}
              </Box>
            ))}
          </Stack>
        ) : (
          // <ChatLoading />
          null
        )}
      </Box>
    </Box>
  );
};

export default MyChats;
